











































import {Component, Vue} from 'vue-property-decorator';
import VueButton from './button/VueButton.vue';
import {ToastPosType, VueToast as VueToastType} from '../types/VueToast';
import VueDetail from './code-presentation/VueDetail.vue';
import VueCodePresentation from './code-presentation/VueCodePresentation.vue';

@Component({
  components: {
    VueButton,
    VueDetail,
    VueCodePresentation
  }
})
export default class ToastsTips extends Vue {
  name = 'ToastsTips';

  showToast0() {
    this.$toast('我是一个VueToast组件');
  }

  showToast11() {
    this.$toast(`你的智商目前为 ${Math.round(Math.random() * 100)}， 需要充值`);
  }

  showToast1() {
    this.$toast('我是一个VueToast组件我是一个VueToast组件我是一个VueToast组件我是一个VueToast组件', {
      propsData: {
        autoCloseDelay: false,
        closeButton: {
          text: '手动关闭'
        }
      }
    });
  }

  showToast2() {
    this.$toast('我是一个VueToast组件', {
      propsData: {
        autoCloseDelay: false,
        closeButton: {
          text: '手动关闭'
        }
      }
    });
  }

  popUpToasts() {
    this.$toast('我是一个VueToast组件，关闭我就执行一个回调', {
      propsData: {
        autoCloseDelay: false,
        closeButton: {
          text: '手动关闭',
          callback(toast: VueToastType) {
            toast.close();
            toast.$toast('关闭后，执行了一个回调', {
              propsData: {
                position: 'middle',
                autoCloseDelay: 2000
              }
            });
          }
        }
      }
    });
  }

  popUpToasts2() {
    this.$toast(`
        <p>
          我是由一个<i>标签</i>包裹<strong>文字</strong>的VueToast组件
        </p>`, {
      propsData: {
        autoCloseDelay: false,
        enableUnsafeHTML: true,
        closeButton: {
          text: '手动关闭',
          callback(toast: VueToastType) {
            toast.close();
          }
        }
      }
    });
  }

  popUpToasts3() {
    this.$toast(`
        <p>
          <a style="color: seagreen;" href="https://cn.vuejs.org">Vue官网链接</a>
        </p>`, {
      propsData: {
        autoCloseDelay: false,
        enableUnsafeHTML: true,
        closeButton: {
          text: '手动关闭',
          callback(toast: VueToastType) {
            toast.close();
          }
        }
      }
    });
  }

  popUpToasts4(position: keyof ToastPosType) {
    const positionMap = {
      'top': '顶部',
      'middle': '中部',
      'bottom': '底部',
    };
    this.$toast(`智商需要充值 出现在${positionMap[position]}`, {
      propsData: {
        position,
        autoCloseDelay: 1200,
        enableUnsafeHTML: true,
        closeButton: {
          text: '已充值',
          callback(toast: VueToastType) {
            toast.close();
          }
        }
      }
    });
  }

}
