





















import {computed, defineComponent, SetupContext} from '@vue/composition-api';

interface PropsType {
  activeText?: string;
  inactiveText?: string;
  toggleValue?: boolean;
  disabled?: boolean;
}

export default defineComponent({
  name: 'VueSwitch',
  props: {
    activeText: {
      type: String,
      default: '',
      validator(value: string) {
        return value.length <= 3;
      }
    },
    inactiveText: {
      type: String,
      default: '',
      validator(value: string) {
        return value.length <= 3;
      }
    },
    toggleValue: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  setup(props: PropsType, ctx: SetupContext) {
    const toggle = () => {
      !props.disabled
          ? ctx.emit('update:toggleValue', !props.toggleValue)
          : null;
    };

    const checked = computed(() => {
      return props.toggleValue;
    });

    const openClose = computed(() => {
      return {
        open: props.toggleValue,
        close: !props.toggleValue,
      };
    });

    const showText = computed(() => {
      return props.toggleValue ? props.activeText : props.inactiveText;
    });

    return {
      toggle,
      checked,
      openClose,
      showText
    };

  }
});

