













import {Component, Vue} from 'vue-property-decorator';

@Component
export default class Nav extends Vue {
  name = 'Nav';
  currentTabText = 'BooksInfo';
  tabs = [
    'Buttons',
    'Inputs',
    'GridSystems',
    'LayoutGroups',
    'ToastsTips',
    'Tabs',
    'Popovers',
    'Collapses',
    'Switches',
    'BooksInfo'
  ];

  showTab(tab: string) {
    // 切换对应样式
    this.currentTabText = tab;
    // 发布 切换对应组件 的自定义事件
    this.$emit('update:tabName', tab);
  }

}
